import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest, interval } from "rxjs";
import { Pit } from "../model/pit";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { DriverConfigService } from "../../driver/service/driver-config.service";
import { PitConfigService } from "./pit-config.service";
import { PitDataService } from "./pit-data.service";

@Injectable({
  providedIn: 'root'
})
export class PitService {
  private readonly worker: Worker | undefined;

  private readonly pitDataService = inject(PitDataService);
  private readonly driverConfigService = inject(DriverConfigService);
  private readonly pitConfigService = inject(PitConfigService);

  readonly activePit: WritableSignal<Pit | undefined> = signal(undefined);
  readonly activePitRemainingMilliseconds: WritableSignal<number> = signal(0);
  readonly lastRefuelPit: WritableSignal<Pit | undefined> = signal(undefined);
  readonly lastInterphoneChangePit: WritableSignal<Pit | undefined> = signal(undefined);
  readonly completedDriverChanges: WritableSignal<number> = signal(0);
  readonly lastPit: WritableSignal<Pit | undefined> = signal(undefined);
  readonly remainingDriverChanges: WritableSignal<number> = signal(0);
  readonly pitAVGMilliseconds: WritableSignal<number> = signal(0);
  readonly remainingMinTotalPitMilliseconds: WritableSignal<number> = signal(0);
  readonly lastKartChangePit: WritableSignal<Pit | undefined> = signal(undefined);
  readonly completedKartChanges: WritableSignal<number> = signal(0);

  constructor() {

    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./pit.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.activePit.set(data.activePit as Pit | undefined);
        this.activePitRemainingMilliseconds.set(data.activePitRemainingMilliseconds);
        this.lastRefuelPit.set(data.lastRefuelPit as Pit | undefined);
        this.lastInterphoneChangePit.set(data.lastInterphoneChangePit as Pit | undefined);
        this.lastPit.set(data.lastPit as Pit | undefined);
        this.completedDriverChanges.set(data.completedDriverChanges);
        this.remainingDriverChanges.set(data.remainingDriverChanges);
        this.pitAVGMilliseconds.set(data.pitAVGMilliseconds);
        this.remainingMinTotalPitMilliseconds.set(data.remainingMinTotalPitMilliseconds);
        this.lastKartChangePit.set(data.lastKartChangePit as Pit | undefined);
        this.completedKartChanges.set(data.completedKartChanges);
      };
    }

    combineLatest({
      pits: toObservable(this.pitDataService.pits),
      driverConfig: toObservable(this.driverConfigService.driverConfig),
      pitConfig: toObservable(this.pitConfigService.pitConfig),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({pits, driverConfig, pitConfig}) => {
      if (this.worker && driverConfig && pitConfig) {
        this.worker.postMessage({
          pits: pits,
          driverConfig: driverConfig,
          pitConfig: pitConfig
        })
      }
    });
  }
}
