import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { RaceService } from "../../race/service/race.service";
import { PitService } from "../../pit/service/pit.service";
import { RaceConfigService } from "../../race/service/race-config.service";
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";

@Injectable({
  providedIn: 'root'
})
export class InterphoneService {
  private readonly raceService = inject(RaceService);
  private readonly pitService = inject(PitService);
  private readonly raceConfigService = inject(RaceConfigService);

  private readonly worker: Worker | undefined;

  readonly lastChangeDate: WritableSignal<Date | undefined> = signal(undefined);
  readonly emptyBatteryDate: WritableSignal<Date | undefined> = signal(undefined);
  readonly remainingBatteryPercentage: WritableSignal<number> = signal(100);

  constructor() {
    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./interphone.worker', import.meta.url));
      this.worker.onmessage = ({data}) => {
        this.lastChangeDate.set(data.lastChangeDate);
        this.emptyBatteryDate.set(data.emptyBatteryDate);
        this.remainingBatteryPercentage.set(data.remainingBatteryPercentage);
      };
    }

    combineLatest({
      lastInterphoneChangePit: toObservable(this.pitService.lastInterphoneChangePit),
      race: toObservable(this.raceService.activeRace),
      raceConfig: toObservable(this.raceConfigService.raceConfig),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({lastInterphoneChangePit, race, raceConfig}) => {
      if (this.worker && raceConfig) {
        this.worker.postMessage({
          lastInterphoneChangePit: lastInterphoneChangePit,
          race: race,
          raceConfig: raceConfig
        });
      }
    });
  }
}
