import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { DeltaStint } from "../model/delta-stint";
import { StintDataService } from "./stint-data.service";

@Injectable({
  providedIn: 'root'
})
export class DeltaStintService {
  private readonly stintDataService = inject(StintDataService);

  private readonly worker: Worker | undefined;

  readonly deltaStints: WritableSignal<DeltaStint[]> = signal([]);
  readonly totalDeltaMilliseconds: WritableSignal<number> = signal(0);
  readonly deltaMilliseconds: WritableSignal<number> = signal(0);


  constructor() {
    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./delta-stint.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.deltaStints.set(data.deltaStints);
        this.totalDeltaMilliseconds.set(data.deltaMilliseconds);
        this.deltaMilliseconds.set(data.deltaMilliseconds);
      };
    }

    combineLatest({
      stints: toObservable(this.stintDataService.stints),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({stints}) => {
      if (this.worker) {
        this.worker.postMessage({
          stints: stints
        })
      }
    })
  }
}
