import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { TimelineStep } from "../model/timeline-step";
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { StintOptimizerService } from "../../stint/service/stint-optimizer.service";
import { PitService } from "../../pit/service/pit.service";
import { TyreService } from "../../tyre/service/tyre.service";
import { PitConfigService } from "../../pit/service/pit-config.service";
import { PitDataService } from "../../pit/service/pit-data.service";
import { StintDataService } from "../../stint/service/stint-data.service";

@Injectable({
  providedIn: 'root'
})
export class TimelineService {
  private readonly stintDataService = inject(StintDataService);
  private readonly pitService = inject(PitService);
  private readonly pitDataService = inject(PitDataService);
  private readonly stintOptimizerService = inject(StintOptimizerService);
  private readonly pitConfigService = inject(PitConfigService);
  private readonly tyreService = inject(TyreService);

  private readonly worker: Worker | undefined;

  readonly timelineSteps: WritableSignal<TimelineStep[]> = signal([]);

  constructor() {

    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./timeline.worker', import.meta.url));
      this.worker.onmessage = ({data}) => {
        this.timelineSteps.set(data.timelineSteps as TimelineStep[]);
      };
    }

    combineLatest({
      stints: toObservable(this.stintDataService.stints),
      pits: toObservable(this.pitDataService.pits),
      remainingDriverChanges: toObservable(this.pitService.remainingDriverChanges),
      pitConfig: toObservable(this.pitConfigService.pitConfig),
      optimizedStint: toObservable(this.stintOptimizerService.optimizedStint),
      remainingTyreChange: toObservable(this.tyreService.remainingTyreChange),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe((
        {
          stints,
          pits,
          remainingDriverChanges,
          pitConfig,
          optimizedStint,
          remainingTyreChange
        }) => {

      if (this.worker && pitConfig) {
        this.worker.postMessage({
          stints: stints,
          pits: pits,
          remainingDriverChanges: remainingDriverChanges,
          pitConfig: pitConfig,
          optimizedStint: optimizedStint,
          remainingTyreChange: remainingTyreChange
        });
      }
    })
  }
}
