import { Component, computed, inject } from '@angular/core';
import { CardComponent } from '../../../shared/component/card/card.component';
import { LapMissingService } from '../../service/lap-missing.service';
import { NgForOf } from '@angular/common';
import { TeamService } from '../../../team/service/team.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-lap-missing',
  imports: [
    CardComponent,
    NgForOf,
    ChipComponent
  ],
  templateUrl: './lap-missing.component.html',
  styleUrl: './lap-missing.component.css'
})
export class LapMissingComponent {

  private readonly lapMissingService = inject(LapMissingService);
  private readonly teamService = inject(TeamService);

  lapMissingCount = this.lapMissingService.lapMissingCount;

  lapMissingRaceNumber = computed(() => {
    const value = this.lapMissingService.lapMissingByRaceNumber()
    return Array.from(value.keys());
  })

  getTeamName(raceNumber: number): string {
    const teamNameByRaceNumber = this.teamService.teamNameByRaceNumber();
    if (teamNameByRaceNumber.has(raceNumber)) {
      return teamNameByRaceNumber.get(raceNumber)!;
    }
    return '';
  }

  getLapMissingString(raceNumber: number): string {
    const lapMissingByRaceNumber = this.lapMissingService.lapMissingByRaceNumber();
    const lapMissing = lapMissingByRaceNumber.get(raceNumber);
    if (lapMissing) {
      return lapMissing.join(', ');
    }
    return '';
  }
}
