import { Component, computed, inject } from '@angular/core';
import { CardComponent } from '../../../shared/component/card/card.component';
import { StintService } from '../../../stint/service/stint.service';
import {
  millisecondsToMinuteSecondMillisecondsString,
  millisecondsToSecondMillisecondsString
} from '../../../util/date.util';
import { LapService } from '../../../lap/service/lap.service';
import { RacePositionService } from '../../../race/service/race-position.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';
import { GapService } from '../../../gap/service/gap.service';

@Component({
  selector: 'app-live-timing',
  imports: [
    CardComponent,
    ChipComponent
  ],
  templateUrl: './live-timing.component.html',
  styleUrl: './live-timing.component.css'
})
export class LiveTimingComponent {

  private readonly stintService = inject(StintService);
  private readonly lapService = inject(LapService);
  private readonly racePositionService = inject(RacePositionService);
  private readonly gapService = inject(GapService);

  readonly currentStintAvgString = computed(() => {
    const stintPaceById = this.stintService.stintPaceById();
    const activeStint = this.stintService.activeStint();

    if (activeStint) {
      return millisecondsToMinuteSecondMillisecondsString(stintPaceById.get(activeStint.id) ?? 0)
    }

    return '--:--.---';
  });

  readonly currentStintBestLapString = computed(() => {
    const stintBestLapById = this.stintService.stintBestLapById();
    const activeStint = this.stintService.activeStint();

    if (activeStint) {
      return millisecondsToMinuteSecondMillisecondsString(stintBestLapById.get(activeStint.id) ?? 0)
    }
    return '--:--.---';
  });

  readonly lastTeamLap = this.lapService.lastTeamLap;
  readonly lastTeamLapString = computed(() => {
      const value = this.lapService.lastTeamLap();
      if (value) {
        return millisecondsToMinuteSecondMillisecondsString(value.lapMilliseconds);
      }
      return '--:--.---';
  });

  readonly lastTeamLapDeltaMilliseconds = this.lapService.lastTeamLapDeltaMilliseconds;

  readonly lastTeamLapDeltaMillisecondsString = computed(() => {
    const value = this.lapService.lastTeamLapDeltaMilliseconds();
    if (value) {
      if (value < 0) {
        return millisecondsToMinuteSecondMillisecondsString(Math.abs(value));
      }
      return millisecondsToSecondMillisecondsString(value);
    }
    return '--.---';
  });

  readonly teamRacePosition = this.racePositionService.teamRacePosition;

  readonly gapAheadMilliseconds = this.gapService.gapAheadMilliseconds;
  readonly gapAheadMillisecondsString = computed(() => {
    const value = this.gapService.gapAheadMilliseconds();
    if (value) {
      return millisecondsToMinuteSecondMillisecondsString(value);
    }
    return '--:--.---';
  });

  readonly gapBehindMilliseconds = this.gapService.gapBehindMilliseconds;
  readonly gapBehindMillisecondsString = computed(() => {
    const value = this.gapService.gapBehindMilliseconds();
    if (value) {
      return millisecondsToMinuteSecondMillisecondsString(Math.abs(value));
    }
    return '--:--.---';
  });

}
