import { Component, computed, effect, inject, Signal } from '@angular/core';
import { NgClass, NgForOf } from "@angular/common";
import { PitService } from "../../service/pit.service";
import { Pit } from "../../model/pit";
import {
  millisecondsToHourMinuteSecondMillisecondString,
  millisecondsToMinuteSecondMillisecondsString
} from "../../../util/date.util";
import { PitDataService } from "../../service/pit-data.service";
import { BoardConfigService } from "../../../board/service/board-config.service";
import { Tooltip } from "../../../shared/component/tooltip/tooltip";
import { TooltipDirective } from "../../../shared/component/tooltip/tooltip.directive";
import { TooltipPosition } from "../../../shared/component/tooltip/tooltip.enums";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';
import { PitConfigService } from '../../service/pit-config.service';

@Component({
  selector: 'app-pit',
  imports: [
    NgClass,
    NgForOf,
    TooltipDirective,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './pit.component.html',
  styleUrl: './pit.component.css'
})
export class PitComponent {
  private readonly pitService = inject(PitService);
  private readonly pitDataService = inject(PitDataService);
  private readonly boardConfigService = inject(BoardConfigService);
  private readonly pitConfigService = inject(PitConfigService);

  readonly pits: Signal<Pit[]> = this.pitDataService.pits;

  readonly activePitId = computed(() => {
    const activePit = this.pitService.activePit();
    if (activePit) {
      return activePit.id;
    }
    return undefined;
  });

  readonly completedDriverChanges: Signal<number> = this.pitService.completedDriverChanges;

  readonly pitAVGMilliseconds = computed(() => {
    const value = this.pitService.pitAVGMilliseconds();
    this.pitAVGString = millisecondsToMinuteSecondMillisecondsString(value);
    return value;
  });

  readonly minTotalPitMillisecondsVisible = computed(() => {
    const config= this.pitConfigService.pitConfig();
    return config ? config.minTotalPitMinute > 0 : false;
  });

  readonly remainingMinTotalPitMilliseconds = computed(() => {
    const value = this.pitService.remainingMinTotalPitMilliseconds();
    this.remainingMinTotalPitString = millisecondsToHourMinuteSecondMillisecondString(value);
    return value;
  });

  tyreChangeVisible = false;
  fuelVisible = false;
  interphoneVisible = false;
  kartChangeVisible = false;

  pitAVGString: string = '';
  remainingMinTotalPitString: string = '';

  protected tooltipAVGInfo: Tooltip = {
    footer: "",
    paragraphs: ["Only completed pit"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  constructor() {
    effect(() => {
      const boardConfig = this.boardConfigService.boardConfig()
      if (boardConfig) {
        this.fuelVisible = boardConfig.fuelVisible;
        this.tyreChangeVisible = boardConfig.tyreChangeVisible;
        this.kartChangeVisible = boardConfig.kartChangeVisible;
        this.interphoneVisible = boardConfig.interphoneVisible;
      }
    });
  }

  calculatePitTime(pit: Pit): string {
    if (pit.exitAt) {
      return millisecondsToMinuteSecondMillisecondsString(pit.exitAt.getTime() - pit.entryAt.getTime());
    }
    return millisecondsToMinuteSecondMillisecondsString(new Date().getTime() - pit.entryAt.getTime());
  }

  calculateTotalPitTime(): string {
    const pits = this.pits();
    let timeSpent = 0;
    pits.forEach(pit => {
      if (pit.exitAt) {
        timeSpent += pit.exitAt.getTime() - pit.entryAt.getTime();
      } else {
        // PIT CORRENTE
        timeSpent += new Date().getTime() - pit.entryAt.getTime();
      }
    })
    return millisecondsToHourMinuteSecondMillisecondString(timeSpent);
  }

  calculateTotalRefueling(): number {
    return this.pits().filter(pit => pit.refuel).length;
  }

  calculateTotalTyreChanges(): number {
    return this.pits().filter(pit => pit.tyreChange).length;
  }

  calculateTotalInterphoneChanges(): number {
    return this.pits().filter(pit => pit.interphoneChange).length;
  }

  calculateTotalKartChanges(): number {
    return this.pits().filter(pit => pit.kartChange).length;
  }
}
