import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest, interval } from "rxjs";
import { Stint } from "../model/stint";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { StintOptimizerService } from "./stint-optimizer.service";
import { LapConfigService } from "../../lap/service/lap-config.service";
import { StintDataService } from "./stint-data.service";
import { LapService } from '../../lap/service/lap.service';

@Injectable({
  providedIn: 'root'
})
export class StintService {

  private readonly worker: Worker | undefined;

  private readonly stintDataService = inject(StintDataService);
  private readonly stintOptimizerService = inject(StintOptimizerService);
  private readonly lapConfigService = inject(LapConfigService);
  private readonly lapService = inject(LapService);

  readonly activeStint: WritableSignal<Stint | undefined> = signal(undefined);
  readonly activeStintTimeOnTrack: WritableSignal<number> = signal(0);
  readonly activeStintLaps: WritableSignal<number> = signal(0);
  readonly activeStintRemainingTimeOnTrack: WritableSignal<number> = signal(0);
  readonly activeStintRemainingLaps: WritableSignal<number> = signal(0);
  readonly activeStintGainedTimeOnTrack: WritableSignal<number> = signal(0);
  readonly activeStintGainedLaps: WritableSignal<number> = signal(0);
  readonly stintAVGMilliseconds: WritableSignal<number> = signal(0);
  readonly stintPaceById: WritableSignal<Map<number,number>> = signal(new Map<number, number>());
  readonly stintBestLapById: WritableSignal<Map<number,number>> = signal(new Map<number, number>());

  constructor() {
    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./stint.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.activeStint.set(data.activeStint as Stint | undefined);
        this.activeStintTimeOnTrack.set(data.activeStintTimeOnTrack);
        this.activeStintLaps.set(data.activeStintLaps);
        this.activeStintRemainingTimeOnTrack.set(data.activeStintRemainingTimeOnTrack);
        this.activeStintRemainingLaps.set(data.activeStintRemainingLaps);
        this.activeStintGainedTimeOnTrack.set(data.activeStintGainedTimeOnTrack);
        this.activeStintGainedLaps.set(data.activeStintGainedLaps);
        this.stintAVGMilliseconds.set(data.stintAVGMilliseconds);
        this.stintPaceById.set(data.stintPaceById);
        this.stintBestLapById.set(data.stintBestLapById);
      };
    }

    combineLatest({
      stints: toObservable(this.stintDataService.stints),
      optimizedStint: toObservable(this.stintOptimizerService.optimizedStint),
      lapConfig: toObservable(this.lapConfigService.lapConfig),
      validTeamPaceLaps: toObservable(this.lapService.validTeamPaceLaps),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({stints, optimizedStint ,lapConfig, validTeamPaceLaps}) => {
      if (this.worker && lapConfig) {
        this.worker.postMessage({
          stints: stints,
          optimizedStint: optimizedStint,
          lapConfig: lapConfig,
          validTeamPaceLaps: validTeamPaceLaps
        });
      }
    })
  }
}
