import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { RaceService } from "../../race/service/race.service";
import { PitService } from "../../pit/service/pit.service";
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { KartConfigService } from "./kart-config.service";
import { RaceConfigService } from "../../race/service/race-config.service";
import { LapConfigService } from "../../lap/service/lap-config.service";

@Injectable({
  providedIn: 'root'
})
export class KartService {
  private readonly worker: Worker | undefined;

  private readonly raceConfigService = inject(RaceConfigService);
  private readonly kartConfigService = inject(KartConfigService);
  private readonly lapConfigService = inject(LapConfigService);
  private readonly raceService = inject(RaceService);
  private readonly pitService = inject(PitService);

  readonly remainingKartChanges: WritableSignal<number> = signal(0);
  readonly remainingKartPercentage: WritableSignal<number> = signal(100);
  readonly remainingKartLap: WritableSignal<number> = signal(0);
  readonly expirationKartDate: WritableSignal<Date | undefined> = signal(undefined);
  readonly lastKartChangeDate: WritableSignal<Date | undefined> = signal(undefined);

  constructor() {

    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./kart.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.remainingKartChanges.set(data.remainingKartChanges);
        this.remainingKartPercentage.set(data.remainingKartPercentage);
        this.remainingKartLap.set(data.remainingKartLap);
        this.expirationKartDate.set(data.expirationKartDate);
        this.lastKartChangeDate.set(data.lastKartChangeDate);
      };
    }

    combineLatest({
      race: toObservable(this.raceService.activeRace),
      raceConfig: toObservable(this.raceConfigService.raceConfig),
      kartConfig: toObservable(this.kartConfigService.kartConfig),
      lapConfig: toObservable(this.lapConfigService.lapConfig),
      lastKartChangePit: toObservable(this.pitService.lastKartChangePit),
      completedKartChanges: toObservable(this.pitService.completedKartChanges),
      ping: interval(1000)
    }).pipe(
        takeUntilDestroyed()
    ).subscribe(({race, raceConfig, kartConfig, lapConfig, lastKartChangePit, completedKartChanges}) => {
      if (this.worker && raceConfig && kartConfig && lapConfig) {
        this.worker.postMessage({
          kartConfig: kartConfig,
          raceConfig: raceConfig,
          lapConfig: lapConfig,
          race: race,
          lastKartChangePit: lastKartChangePit,
          completedKartChanges: completedKartChanges
        });
      }
    })
  }
}
