import { Component, computed, inject, Signal } from '@angular/core';
import { BatteryComponent } from "../../../shared/component/battery/battery.component";
import { DatePipe, NgClass } from "@angular/common";
import { FormsModule } from "@angular/forms";
import { KartService } from "../../service/kart.service";
import { getElapsedTime, getTimeUntilFutureDate } from "../../../util/date.util";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-kart',
  imports: [
    BatteryComponent,
    DatePipe,
    FormsModule,
    NgClass,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './kart.component.html',
  styleUrl: './kart.component.css'
})
export class KartComponent {

  private readonly kartService = inject(KartService);

  readonly remainingKartPercentage = this.kartService.remainingKartPercentage;
  readonly remainingKartLap = this.kartService.remainingKartLap;
  readonly expirationKartDate: Signal<Date | undefined> = this.kartService.expirationKartDate;
  readonly lastKartChangeDate: Signal<Date | undefined> = this.kartService.lastKartChangeDate;

  expirationKartCountdown = computed(() => {
    return getTimeUntilFutureDate(this.expirationKartDate())
  });

  sinceLastKartChange = computed(() => {
    return getElapsedTime(this.lastKartChangeDate(), new Date());
  });

  remainingColor = computed(() => {
    return this.getRemainingColor(this.remainingKartPercentage());
  });

  warning = computed(() => {
    return this.remainingKartPercentage() <= 20;
  });

  private getRemainingColor(kartPercentage: number): string {
    if (kartPercentage <= 10) {
      return 'bg-red-500';
    }
    if (kartPercentage <= 25) {
      return 'bg-orange-500';
    }
    if (kartPercentage <= 50) {
      return 'bg-yellow-500';
    }
    return 'bg-green-500';
  }
}
