import { Component, inject, Signal } from '@angular/core';
import { KartService } from "../../service/kart.service";
import { PitService } from "../../../pit/service/pit.service";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-kart-change',
  imports: [
    CardComponent,
    ChipComponent
  ],
  templateUrl: './kart-change.component.html',
  styleUrl: './kart-change.component.css'
})
export class KartChangeComponent {

  private readonly kartService = inject(KartService);
  private readonly pitService = inject(PitService);

  readonly remainingKartChanges: Signal<number> = this.kartService.remainingKartChanges;
  readonly completedKartChanges: Signal<number> = this.pitService.completedKartChanges;
}
