import { Component, computed, inject, Signal } from '@angular/core';
import { CardComponent } from '../../../shared/component/card/card.component';
import { DriverService } from '../../service/driver.service';
import { Driver } from '../../model/driver';
import { NgClass, NgForOf } from '@angular/common';
import { StintService } from '../../../stint/service/stint.service';
import { millisecondsToMinuteSecondMillisecondsString } from '../../../util/date.util';
import { LapService } from '../../../lap/service/lap.service';
import { Lap } from '../../../lap/model/lap';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-driver-performance',
  imports: [
    CardComponent,
    NgForOf,
    NgClass,
    ChipComponent
  ],
  templateUrl: './driver-performance.component.html',
  styleUrl: './driver-performance.component.css'
})
export class DriverPerformanceComponent {

  private readonly driverService = inject(DriverService);
  private readonly stintService = inject(StintService);
  private readonly lapService = inject(LapService);

  readonly drivers: Signal<Driver[]> = this.driverService.drivers;
  readonly teamBestLap: Signal<Lap | undefined> = this.lapService.teamBestLap;

  readonly activeStintDriverId = computed(() => {
    const activeStint = this.stintService.activeStint();
    if (activeStint) {
      return activeStint.driverId;
    }
    return undefined;
  });

  readonly driverBestLapStringById: Signal<Map<number, string>> = computed(() => {
    const result = new Map<number, string>;
    const value = this.driverService.driverBestLapById()
    value.forEach((lap, id) => {
      result.set(id, millisecondsToMinuteSecondMillisecondsString(lap));
    });
    return result;
  });

  readonly driverPaceStringById: Signal<Map<number, string>> = computed(() => {
    const result = new Map<number, string>;
    const value = this.driverService.driverPaceById()
    value.forEach((pace, id) => {
      result.set(id, millisecondsToMinuteSecondMillisecondsString(pace));
    });
    return result;
  })

  readonly teamBestLapString: Signal<string | undefined> = computed(() => {
    const value = this.lapService.teamBestLap();
    if (value) {
      return millisecondsToMinuteSecondMillisecondsString(value.lapMilliseconds);
    }
    return undefined;
  });

  readonly bestPaceString: Signal<string | undefined> = computed(() => {
    const value = this.driverService.driverBestPaceMillisecond();
    if (value) {
      return millisecondsToMinuteSecondMillisecondsString(value);
    }
    return undefined;
  });

  readonly lastCompletedStintPaceStringByDriverId: Signal<Map<number, string>> = computed(() => {
    const result = new Map<number, string>();
    const driverLastStintIdByDriverId = this.driverService.driverLastStintIdByDriverId();
    const stintPaceById = this.stintService.stintPaceById();
    driverLastStintIdByDriverId.forEach((stintId, driverId) => {
      const pace = stintPaceById.get(stintId);
      if (pace) {
        result.set(driverId, millisecondsToMinuteSecondMillisecondsString(pace));
      }
    })
    return result;
  });

  getBestLap(driverId: number): string {
    return this.driverBestLapStringById().get(driverId) ?? '--:--.---';
  }

  getPace(driverId: number): string {
    return this.driverPaceStringById().get(driverId) ?? '--:--.---';
  }

  isBestLap(driverId: number): boolean {
    const value = this.lapService.teamBestLap();
    if (value) {
      return value.driverId === driverId;
    }
    return false;
  }

  isBestPace(driverId: number): boolean {
    const value = this.driverService.driverIdBestPace();
    if (value) {
      return value === driverId;
    }
    return false;
  }

  getLastStintPace(driverId: number): string {
    return this.lastCompletedStintPaceStringByDriverId().get(driverId) ?? '--:--.---';
  }
}
