import { Component, inject } from '@angular/core';
import { CardComponent } from "../../../shared/component/card/card.component";
import {
  SteeringWheelMessageUpdateService
} from '../../service/steering-wheel-message-update.service';
import { getButtonBackgroundColor, SteeringWheelMessage } from '../../model/steering-wheel-message';

@Component({
  selector: 'app-steering-wheel-message-button',
  imports: [
    CardComponent
  ],
  templateUrl: './steering-wheel-message-button.component.html',
  styleUrl: './steering-wheel-message-button.component.css'
})
export class SteeringWheelMessageButtonComponent {

  private readonly steeringWheelMessageUpdateService = inject(SteeringWheelMessageUpdateService);

  private boxMessage: SteeringWheelMessage = {
    type: "box"
  }

  private safetyCarMessage: SteeringWheelMessage = {
    type: "safety-car"
  }

  private fastDriverBehindMessage: SteeringWheelMessage = {
    type: "fast-driver-behind"
  }

  sendBoxMessage() {
   this.steeringWheelMessageUpdateService.updateMessageType({
     type: "box"
   });
  }

  sendSafetyCarMessage() {
    this.steeringWheelMessageUpdateService.updateMessageType({
      type: "safety-car"
    });
  }

  sendFastDriverBehindMessage() {
    this.steeringWheelMessageUpdateService.updateMessageType({
      type: "fast-driver-behind"
    });
  }

  getBoxButtonBackgroundColor(): string {
    return getButtonBackgroundColor(this.boxMessage);
  }

  getSafetyCarButtonBackgroundColor(): string {
    return getButtonBackgroundColor(this.safetyCarMessage);
  }

  getFastDriverBehindButtonBackgroundColor(): string {
    return getButtonBackgroundColor(this.fastDriverBehindMessage);
  }
}
