import { computed, inject, Injectable, Signal } from '@angular/core';
import { RaceService } from "./race.service";
import { Race } from "../model/race";
import { StintOptimizerService } from "../../stint/service/stint-optimizer.service";
import { Observable } from "rxjs";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { LiveTimingConfigService } from "../../live-timing/service/live-timing-config.service";
import { NotificationService } from '../../shared/service/notification.service';
import { BoardConfigService } from '../../board/service/board-config.service';
import { TyreConfigService } from '../../tyre/service/tyre-config.service';

@Injectable({
  providedIn: 'root'
})
export class RaceManagerService {

  private readonly httpClient = inject(HttpClient);
  private readonly raceManagerPath = "/race-manager";

  private readonly raceService = inject(RaceService);
  private readonly notificationService = inject(NotificationService);
  private readonly liveTimingConfigService = inject(LiveTimingConfigService);
  private readonly stintOptimizerService = inject(StintOptimizerService);
  private readonly boardConfigService = inject(BoardConfigService);
  private readonly tyreConfigService = inject(TyreConfigService);

  private readonly activeRace: Signal<Race | undefined> = this.raceService.activeRace;

  private readonly optimizedStintMilliseconds: Signal<number> = computed(() => {
    const optimizedStint = this.stintOptimizerService.optimizedStint();
    return optimizedStint ? optimizedStint.avgStintMillisecondsTime : 0;
  });

  private readonly liveTimingUrl: Signal<string | undefined> = computed(() => {
    const config = this.liveTimingConfigService.liveTimingConfig();
    return config ? config.url : undefined;
  });

  private readonly liveTimingSource: Signal<string | undefined> = computed(() => {
    const config = this.liveTimingConfigService.liveTimingConfig();
    return config ? config.source : undefined;
  });

  private readonly liveTimingEnabled: Signal<boolean> = computed(() => {
    const config = this.boardConfigService.boardConfig();
    return config ? config.liveTimingVisible : false;
  });

  private readonly fuelEnabled: Signal<boolean> = computed(() => {
    const config = this.boardConfigService.boardConfig();
    return config ? config.fuelVisible : false;
  });

  private readonly tyreChangeEnabled: Signal<boolean> = computed(() => {
    const config = this.boardConfigService.boardConfig();
    return config ? config.tyreChangeVisible : false;
  });

  private readonly interphoneEnabled: Signal<boolean> = computed(() => {
    const config = this.boardConfigService.boardConfig();
    return config ? config.interphoneVisible : false;
  });

  private readonly kartChangeEnabled: Signal<boolean> = computed(() => {
    const config = this.boardConfigService.boardConfig();
    return config ? config.kartChangeVisible : false;
  });

  private readonly tyrePressureEnabled: Signal<boolean> = computed(() => {
    const config = this.boardConfigService.boardConfig();
    return config ? config.tyrePressureVisible : false;
  });

  private readonly frontLeftTyrePressurePsi: Signal<number | undefined> = computed(() => {
    const config = this.tyreConfigService.tyreConfig();
    return config ? config.frontLeftTyrePressurePsi : undefined;
  });

  private readonly frontRightTyrePressurePsi: Signal<number | undefined> = computed(() => {
    const config = this.tyreConfigService.tyreConfig();
    return config ? config.frontRightTyrePressurePsi : undefined;
  });

  private readonly rearLeftTyrePressurePsi: Signal<number | undefined> = computed(() => {
    const config = this.tyreConfigService.tyreConfig();
    return config ? config.rearLeftTyrePressurePsi : undefined;
  });

  private readonly rearRightTyrePressurePsi: Signal<number | undefined> = computed(() => {
    const config = this.tyreConfigService.tyreConfig();
    return config ? config.rearRightTyrePressurePsi : undefined;
  });

  public startRace(startedDriverId: number) {
    const liveTimingUrl = this.liveTimingEnabled() ? this.liveTimingUrl() || undefined : undefined;
    const liveTimingSource = this.liveTimingEnabled() ? this.liveTimingSource() || undefined : undefined;

    const frontLeftTyrePressurePsi = this.tyrePressureEnabled() ? this.frontLeftTyrePressurePsi() : undefined;
    const frontRightTyrePressurePsi = this.tyrePressureEnabled() ? this.frontRightTyrePressurePsi() : undefined;
    const rearLeftTyrePressurePsi = this.tyrePressureEnabled() ? this.rearLeftTyrePressurePsi() : undefined;
    const rearRightTyrePressurePsi = this.tyrePressureEnabled() ? this.rearRightTyrePressurePsi() : undefined;

    this.sendStartRace(
      startedDriverId,
      new Date(),
      this.optimizedStintMilliseconds(),
      liveTimingUrl,
      liveTimingSource,
      frontLeftTyrePressurePsi,
      frontRightTyrePressurePsi,
      rearLeftTyrePressurePsi,
      rearRightTyrePressurePsi,
    ).subscribe(() => {
        this.notificationService.success('Race started');
    });
  }

  public endRace() {
    const activeRace = this.activeRace();
    if (activeRace === undefined) {
      return;
    }

    this.sendEndRace(
      activeRace.id,
      new Date(),
    ).subscribe(() => {
      this.notificationService.success('Race ended');
    });
  }

  public pitIn(
    exitDriverId: number,
    refuel: boolean,
    tyreChange: boolean,
    interphoneChange: boolean,
    kartChange: boolean
  ) {
    const activeRace = this.activeRace();
    if (activeRace === undefined) {
      return;
    }

    const refuelSent = this.fuelEnabled() ? refuel : false;
    const tyreChangeSent = this.tyreChangeEnabled() ? tyreChange : false;
    const interphoneChangeSent = this.interphoneEnabled() ? interphoneChange : false;
    const kartChangeSent = this.kartChangeEnabled() ? kartChange : false;

    this.sendPitIn(
      activeRace.id,
      exitDriverId,
      refuelSent,
      tyreChangeSent,
      interphoneChangeSent,
      kartChangeSent
    ).subscribe(() => {
      this.notificationService.success('Pit in');
    });
  }

  public pitOut(): Observable<boolean> {
    const activeRace = this.activeRace();
    if (activeRace === undefined) {
      return new Observable<boolean>(observer => {
        observer.next(false);
        observer.complete();
      });
    }

    const frontLeftTyrePressurePsi = this.tyrePressureEnabled() ? this.frontLeftTyrePressurePsi() : undefined;
    const frontRightTyrePressurePsi = this.tyrePressureEnabled() ? this.frontRightTyrePressurePsi() : undefined;
    const rearLeftTyrePressurePsi = this.tyrePressureEnabled() ? this.rearLeftTyrePressurePsi() : undefined;
    const rearRightTyrePressurePsi = this.tyrePressureEnabled() ? this.rearRightTyrePressurePsi() : undefined;


    return new Observable<boolean>(observer => {
      this.sendPitOut(
        activeRace.id,
        this.optimizedStintMilliseconds(),
        new Date(),
        frontLeftTyrePressurePsi,
        frontRightTyrePressurePsi,
        rearLeftTyrePressurePsi,
        rearRightTyrePressurePsi,
      ).subscribe({
        next: () => {
          this.notificationService.success('Pit out');
          observer.next(true);
          observer.complete();
        }
      });
    });
  }

  private sendStartRace(
    startedDriverId: number,
    startedAt: Date,
    optimizedStintMilliseconds: number,
    liveTimingUrl: string | undefined,
    liveTimingSource: string | undefined,
    frontLeftTyrePressurePsi: number | undefined,
    frontRightTyrePressurePsi: number | undefined,
    rearLeftTyrePressurePsi: number | undefined,
    rearRightTyrePressurePsi: number | undefined,
  ): Observable<void> {
    return this.httpClient
    .post<void>(
      `${environment.baseUrl}${this.raceManagerPath}/start-race`,
      {
        startedDriverId: startedDriverId,
        startedAt: startedAt,
        optimizedStintMilliseconds: optimizedStintMilliseconds,
        liveTimingUrl: liveTimingUrl,
        liveTimingSource: liveTimingSource,
        frontLeftTyrePressurePsi: frontLeftTyrePressurePsi,
        frontRightTyrePressurePsi: frontRightTyrePressurePsi,
        rearLeftTyrePressurePsi: rearLeftTyrePressurePsi,
        rearRightTyrePressurePsi: rearRightTyrePressurePsi,
      });
  }

  private sendEndRace(
    raceId: number,
    endedAt: Date,
  ): Observable<void> {
    return this.httpClient
    .post<void>(
        `${environment.baseUrl}${this.raceManagerPath}/end-race`,
        {
          raceId: raceId,
          endedAt: endedAt,
        });
  }

  private sendPitIn(
    raceId: number,
    exitDriverId: number,
    refuel: boolean,
    tyreChange: boolean,
    interphoneChange: boolean,
    kartChange: boolean
  ): Observable<void> {
    return this.httpClient
    .post<void>(
        `${environment.baseUrl}${this.raceManagerPath}/pit-in`,
        {
          raceId: raceId,
          exitDriverId: exitDriverId,
          entryAt: new Date(),
          refuel: refuel,
          tyreChange: tyreChange,
          interphoneChange: interphoneChange,
          kartChange: kartChange,
        });
  }

  public sendPitOut(
    raceId: number,
    optimizedStintMilliseconds: number,
    exitAt: Date,
    frontLeftTyrePressurePsi: number | undefined,
    frontRightTyrePressurePsi: number | undefined,
    rearLeftTyrePressurePsi: number | undefined,
    rearRightTyrePressurePsi: number | undefined,
  ): Observable<void> {
    return this.httpClient
    .post<void>(
        `${environment.baseUrl}${this.raceManagerPath}/pit-out`,
        {
          raceId: raceId,
          optimizedStintMilliseconds: optimizedStintMilliseconds,
          exitAt: exitAt,
          frontLeftTyrePressurePsi: frontLeftTyrePressurePsi,
          frontRightTyrePressurePsi: frontRightTyrePressurePsi,
          rearLeftTyrePressurePsi: rearLeftTyrePressurePsi,
          rearRightTyrePressurePsi: rearRightTyrePressurePsi,
        });
  }
}
