import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { RaceConfigService } from "../../race/service/race-config.service";
import { RaceService } from "../../race/service/race.service";
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { PitService } from "../../pit/service/pit.service";
import { OptimizedStint } from "../model/optimized-stint";
import { TyreService } from "../../tyre/service/tyre.service";
import { LapConfigService } from "../../lap/service/lap-config.service";
import { PitConfigService } from "../../pit/service/pit-config.service";

@Injectable({
  providedIn: 'root'
})
export class StintOptimizerService {
  private readonly raceService = inject(RaceService);
  private readonly pitService = inject(PitService);
  private readonly raceConfigService = inject(RaceConfigService);
  private readonly lapConfigService = inject(LapConfigService);
  private readonly pitConfigService = inject(PitConfigService);
  private readonly tyreService = inject(TyreService);

  private readonly worker: Worker | undefined;

  readonly optimizedStint: WritableSignal<OptimizedStint | undefined> = signal(undefined);

  constructor() {

    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./stint-optimizer.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.optimizedStint.set(data.optimizedStint);
      };
    }

    combineLatest({
      activeRace: toObservable(this.raceService.activeRace),
      willEndRaceDate: toObservable(this.raceService.willEndRaceDate),

      remainingDriverChanges: toObservable(this.pitService.remainingDriverChanges),
      remainingTyreChange: toObservable(this.tyreService.remainingTyreChange),
      lastPit: toObservable(this.pitService.lastPit),
      lapConfig: toObservable(this.lapConfigService.lapConfig),
      raceConfig: toObservable(this.raceConfigService.raceConfig),
      pitConfig: toObservable(this.pitConfigService.pitConfig),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({
                  activeRace,
                  willEndRaceDate,
                  remainingDriverChanges,
                  remainingTyreChange,
                  lastPit,
                  lapConfig,
                  raceConfig,
                  pitConfig
                }) => {
      if (this.worker && raceConfig && lapConfig && pitConfig) {
        this.worker.postMessage({
          activeRace: activeRace,
          willEndRaceDate: willEndRaceDate,
          remainingDriverChanges: remainingDriverChanges,
          remainingTyreChange: remainingTyreChange,
          lastPit: lastPit,
          raceConfig: raceConfig,
          lapConfig: lapConfig,
          pitConfig: pitConfig
        })
      }
    });
  }
}
