import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { RaceService } from "../../race/service/race.service";
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { TyreConfigService } from "./tyre-config.service";
import { PitDataService } from "../../pit/service/pit-data.service";

@Injectable({
  providedIn: 'root'
})
export class TyreService {
  private readonly raceService = inject(RaceService);
  private readonly pitDataService = inject(PitDataService);
  private readonly tyreConfigService = inject(TyreConfigService);

  private readonly worker: Worker | undefined;

  readonly openingTime: WritableSignal<Date | undefined> = signal(undefined)
  readonly closingTime: WritableSignal<Date | undefined> = signal(undefined);

  readonly tyreChangeWindowOpen: WritableSignal<boolean> = signal(false);
  readonly remainingTyreChange: WritableSignal<number> = signal(0);

  constructor() {
    if (typeof Worker !== 'undefined') {
      // Create a new
      this.worker = new Worker(new URL('./tyre.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.openingTime.set(data.openingTime);
        this.closingTime.set(data.closingTime);
        this.tyreChangeWindowOpen.set(data.tyreChangeWindowOpen);
        this.remainingTyreChange.set(data.remainingTyreChange);
      };
    }

    combineLatest({
      pits: toObservable(this.pitDataService.pits),
      race: toObservable(this.raceService.activeRace),
      tyreConfig: toObservable(this.tyreConfigService.tyreConfig),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({pits, race, tyreConfig}) => {

      if (this.worker && tyreConfig) {
        this.worker.postMessage({
          pits: pits,
          race: race,
          tyreConfig: tyreConfig
        });
      }
    });
  }
}
