import { Component, computed, inject, Signal } from '@angular/core';
import { DatePipe, NgClass, NgForOf } from "@angular/common";
import { StintService } from "../../service/stint.service";
import { DriverService } from "../../../driver/service/driver.service";
import {
  getElapsedTime,
  millisecondsToHourMinuteSecondString,
  millisecondsToMinuteSecondMillisecondsString,
} from "../../../util/date.util";
import { Stint } from "../../model/stint";
import { addMilliseconds, format } from "date-fns";
import { StintDataService } from "../../service/stint-data.service";
import { Tooltip } from "../../../shared/component/tooltip/tooltip";
import { TooltipPosition } from "../../../shared/component/tooltip/tooltip.enums";
import { TooltipDirective } from "../../../shared/component/tooltip/tooltip.directive";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-stint',
  imports: [
    NgClass,
    NgForOf,
    DatePipe,
    TooltipDirective,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './stint.component.html',
  styleUrl: './stint.component.css'
})
export class StintComponent {
  private readonly stintService = inject(StintService);
  private readonly stintDataService = inject(StintDataService);
  private readonly driverService = inject(DriverService);

  readonly stints: Signal<Stint[]> = this.stintDataService.stints;

  readonly activeStintId = computed(() => {
    const activeStint = this.stintService.activeStint();
    if (activeStint) {
      return activeStint.id;
    }
    return undefined;
  });

  readonly stintAVGMilliseconds = computed(() => {
    const value = this.stintService.stintAVGMilliseconds();
    this.stintAVGString = millisecondsToHourMinuteSecondString(value);
    return value;
  });

  readonly stintPaceStringById = computed(() => {
    const result = new Map<number, string>();
    const value = this.stintService.stintPaceById();
    value.forEach((pace, id) => {
      result.set(id, millisecondsToMinuteSecondMillisecondsString(pace));
    })
    return result;
  });

  stintAVGString: string = '';

  protected tooltipAVGInfo: Tooltip = {
    footer: "",
    paragraphs: ["Only completed stint"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  getDriverRacingName(driverId: number): string {
    const raceName = this.driverService.raceNameById().get(driverId);
    return raceName ? raceName : '';
  }

  calculateTimeOnTrack(start: Date, end: Date | undefined): string {
    if (end) {
      return getElapsedTime(start, end);
    }
    return getElapsedTime(start, new Date());
  }

  calculateTotalTimeOnTrack(stints: Stint[]): string {
    let timeSpent = 0;
    stints.forEach(stint => {
      if (stint.endedAt) {
        timeSpent += stint.endedAt.getTime() - stint.startedAt.getTime();
      } else {
        // STINT CORRENTE
        timeSpent += new Date().getTime() - stint.startedAt.getTime();
      }
    })
    return millisecondsToHourMinuteSecondString(timeSpent);
  }

  getEndDate(stint: Stint): string {
    if (stint.endedAt) {
      return format(stint.endedAt, 'HH:mm:ss');
    }

    const provisionedEnd = addMilliseconds(stint.startedAt, stint.optimizedMilliseconds);
    return '(' + format(provisionedEnd, 'HH:mm:ss') + ')';
  }

  getPace(stint: Stint): string {
    return this.stintPaceStringById().get(stint.id) ?? '--:--.---';
  }
}
