import { Component, effect, inject } from '@angular/core';
import { ActiveStintComponent } from "../../../stint/component/active-stint/active-stint.component";
import { DeltaStintComponent } from "../../../stint/component/delta-stint/delta-stint.component";
import {
  DriverChangeComponent
} from "../../../driver/component/driver-change/driver-change.component";
import { DriverComponent } from "../../../driver/component/driver/driver.component";
import { FuelComponent } from "../../../fuel/component/fuel/fuel.component";
import { InterphoneComponent } from "../../../interphone/component/interphone/interphone.component";
import { KartChangeComponent } from "../../../kart/component/kart-change/kart-change.component";
import { KartComponent } from "../../../kart/component/kart/kart.component";
import { PitComponent } from "../../../pit/component/pit/pit.component";
import { PitLaneComponent } from "../../../pit-lane/component/pit-lane/pit-lane.component";
import { RaceButtonComponent } from "../../../race/component/race-button/race-button.component";
import { RaceComponent } from "../../../race/component/race/race.component";
import { StintComponent } from "../../../stint/component/stint/stint.component";
import {
  StintOptimizerComponent
} from "../../../stint/component/stint-optimizer/stint-optimizer.component";
import { TimelineComponent } from "../../../timeline/component/timeline/timeline.component";
import { TyreChangeComponent } from "../../../tyre/component/tyre-change/tyre-change.component";
import { BoardConfigService } from "../../service/board-config.service";
import { Title } from "@angular/platform-browser";
import { NavigatorComponent } from "../../../shared/component/navigator/navigator.component";
import {
  LiveTimingComponent
} from "../../../live-timing/component/live-timing/live-timing.component";
import {
  TyrePressureComponent
} from "../../../tyre/component/tyre-pressure/tyre-pressure.component";
import {
  DriverPerformanceComponent
} from "../../../driver/component/driver-performance/driver-performance.component";
import {
  LapMissingComponent
} from '../../../lap-missing/component/lap-missing/lap-missing.component';
import {
  SteeringWheelMessageButtonComponent
} from '../../../steering-wheel/component/steering-wheel-message-button/steering-wheel-message-button.component';

@Component({
  selector: 'app-board',
  imports: [
    ActiveStintComponent,
    DeltaStintComponent,
    DriverChangeComponent,
    DriverComponent,
    FuelComponent,
    InterphoneComponent,
    KartChangeComponent,
    KartComponent,
    PitComponent,
    PitLaneComponent,
    RaceButtonComponent,
    RaceComponent,
    StintComponent,
    StintOptimizerComponent,
    TimelineComponent,
    TyreChangeComponent,
    NavigatorComponent,
    LiveTimingComponent,
    TyrePressureComponent,
    DriverPerformanceComponent,
    LapMissingComponent,
    SteeringWheelMessageButtonComponent
  ],
  templateUrl: './board.component.html',
  styleUrl: './board.component.css'
})
export class BoardComponent {
  private readonly titleService = inject(Title);

  private readonly boardConfigService = inject(BoardConfigService);

  fuelVisible = true;
  pitLaneVisible = true;
  tyreChangeVisible = true;
  interphoneVisible = true;
  driverChangeVisible = true;
  pitVisible = true
  timelineVisible = true;
  deltaStintVisible = true;
  driverVisible = true;
  stintVisible = true;
  activeStintVisible = true;
  stintOptimizerVisible = true;
  kartChangeVisible = true;
  kartVisible = true;
  lapMissingVisible = true;
  driverPerformanceVisible = true;
  liveTimingVisible = true;
  steeringWheelMessageButtonVisible = true;
  tyrePressureVisible = true;

  constructor() {
    this.titleService.setTitle('WBR');

    effect(() => {
      const config = this.boardConfigService.boardConfig()
      if (config) {
        this.fuelVisible = config.fuelVisible;
        this.pitLaneVisible = config.pitLaneVisible;
        this.tyreChangeVisible = config.tyreChangeVisible;
        this.interphoneVisible = config.interphoneVisible;
        this.driverChangeVisible = config.driverChangeVisible;
        this.pitVisible = config.pitVisible;
        this.timelineVisible = config.timelineVisible;
        this.deltaStintVisible = config.deltaStintVisible;
        this.driverVisible = config.driverVisible;
        this.stintVisible = config.stintVisible;
        this.activeStintVisible = config.activeStintVisible;
        this.stintOptimizerVisible = config.stintOptimizerVisible;
        this.kartChangeVisible = config.kartChangeVisible;
        this.kartVisible = config.kartVisible;
        this.lapMissingVisible = config.lapMissingVisible;
        this.driverPerformanceVisible = config.driverPerformanceVisible;
        this.liveTimingVisible = config.liveTimingVisible;
        this.steeringWheelMessageButtonVisible = config.steeringWheelMessageButtonVisible;
        this.tyrePressureVisible = config.tyrePressureVisible;
      }
    });
  }
}
