import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest, interval } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { RaceService } from "../../race/service/race.service";
import { StintService } from "../../stint/service/stint.service";
import { RaceConfigService } from "../../race/service/race-config.service";
import { PitLaneConfigService } from "./pit-lane-config.service";

@Injectable({
  providedIn: 'root'
})
export class PitLaneService {
  private readonly raceService = inject(RaceService);
  private readonly stintService = inject(StintService);
  private readonly raceConfigService = inject(RaceConfigService);
  private readonly pitLaneConfigService = inject(PitLaneConfigService);

  private readonly worker: Worker | undefined;

  readonly open: WritableSignal<boolean> = signal(false);
  readonly openInMilliseconds: WritableSignal<number> = signal(0);

  constructor() {

    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./pit-lane.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.open.set(data.open);
        this.openInMilliseconds.set(data.openInMilliseconds);
      };
    }

    combineLatest({
      willEndRaceDate: toObservable(this.raceService.willEndRaceDate),
      activeStint: toObservable(this.stintService.activeStint),
      raceConfig: toObservable(this.raceConfigService.raceConfig),
      pitLaneConfig: toObservable(this.pitLaneConfigService.pitLaneConfig),
      ping: interval(1000)
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({willEndRaceDate, activeStint, raceConfig, pitLaneConfig}) => {
      if (this.worker && raceConfig) {
        this.worker.postMessage({
          willEndRaceDate: willEndRaceDate,
          activeStint: activeStint,
          raceConfig: raceConfig,
          pitLaneConfig: pitLaneConfig,
        });
      }
    })
  }
}
