import { Component, computed, effect, inject, Signal } from '@angular/core';
import { NgForOf } from "@angular/common";
import { PitLaneService } from "../../service/pit-lane.service";
import { PitService } from "../../../pit/service/pit.service";
import { FormsModule } from "@angular/forms";
import { Driver } from "../../../driver/model/driver";
import { DriverService } from "../../../driver/service/driver.service";
import { Pit } from "../../../pit/model/pit";
import {
  millisecondsToHourMinuteSecondString,
  millisecondsToMinuteSecondString
} from "../../../util/date.util";
import { RaceManagerService } from "../../../race/service/race-manager.service";
import { TyreService } from "../../../tyre/service/tyre.service";
import { AudioService } from "../../../shared/service/audio.service";
import { PitLaneConfigService } from "../../service/pit-lane-config.service";
import { BoardConfigService } from "../../../board/service/board-config.service";
import { CardComponent } from '../../../shared/component/card/card.component';
import { PitConfigService } from '../../../pit/service/pit-config.service';
import { ChipComponent } from '../../../shared/component/chip/chip.component';

@Component({
  selector: 'app-pit-lane',
  imports: [
    FormsModule,
    NgForOf,
    CardComponent,
    ChipComponent
  ],
  templateUrl: './pit-lane.component.html',
  styleUrl: './pit-lane.component.css'
})
export class PitLaneComponent {
  private readonly pitLaneService = inject(PitLaneService);
  private readonly pitService = inject(PitService);
  private readonly driverService = inject(DriverService);
  private readonly raceManagerService = inject(RaceManagerService);
  private readonly tyreService = inject(TyreService);
  private readonly boardConfigService = inject(BoardConfigService);
  private readonly audioService = inject(AudioService);
  private readonly pitLaneConfigService = inject(PitLaneConfigService);
  private readonly pitConfigService = inject(PitConfigService);

  readonly pitLaneOpen: Signal<boolean> = this.pitLaneService.open;
  readonly pitLaneOpenInMilliseconds = computed(() => {
    const value = this.pitLaneService.openInMilliseconds();
    this.pitLaneOpenInMillisecondsString = millisecondsToHourMinuteSecondString(value);
    return value;
  });

  readonly activePit: Signal<Pit | undefined> = this.pitService.activePit;
  readonly activePitRemainingMilliseconds = computed(() => {
    const value = this.pitService.activePitRemainingMilliseconds();
    this.activePitRemainingMillisecondsString = millisecondsToMinuteSecondString(value);
    return value
  })

  readonly drivers: Signal<Driver[]> = this.driverService.drivers;

  readonly remainingTyreChange: Signal<number> = this.tyreService.remainingTyreChange
  readonly tyreChangeWindowOpen: Signal<boolean> = this.tyreService.tyreChangeWindowOpen;

  readonly minPitSeconds = computed(() => {
    const config = this.pitConfigService.pitConfig();
    if (config) {
      return config.minPitSeconds !== 0 ? config.minPitSeconds : undefined;
    }
    return undefined;
  });

  readonly minPitSecondsString = computed(() => {
    const value = this.minPitSeconds();
    if (value) {
      return millisecondsToMinuteSecondString(value * 1000);
    }
    return '';
  });

  readonly maxPitSeconds = computed(() => {
    const config = this.pitConfigService.pitConfig();
    if (config) {
      if (config.maxPitSeconds === config.minPitSeconds) {
        return undefined;
      }
      return config.maxPitSeconds !== 0 ? config.maxPitSeconds : undefined;
    }
    return undefined;
  });

  readonly maxPitSecondsString = computed(() => {
    const value = this.maxPitSeconds();
    if (value) {
      return millisecondsToMinuteSecondString(value * 1000);
    }
    return '';
  });

  fuelVisible = false;
  tyreChangeVisible = false;
  interphoneChangeVisible = false;
  kartChangeVisible = false;

  refuel = false;
  tyreChange = false;
  interphoneChange = false;
  kartChange = false;

  selectedDriver: number = 1;

  pitLaneOpenInMillisecondsString: string = '';
  activePitRemainingMillisecondsString: string = '';

  constructor() {

    effect(() => {
      const pitLaneConfig = this.pitLaneConfigService.pitLaneConfig();
      if (pitLaneConfig) {
        this.refuel = pitLaneConfig.refuel;
        this.tyreChange = pitLaneConfig.tyreChange;
        this.interphoneChange = pitLaneConfig.interphoneChange
        this.selectedDriver = pitLaneConfig.driverId;
        this.kartChange = pitLaneConfig.kartChange;
      }
    });

    effect(() => {
      const boardConfig = this.boardConfigService.boardConfig()
      if (boardConfig) {
        this.fuelVisible = boardConfig.fuelVisible;
        this.tyreChangeVisible = boardConfig.tyreChangeVisible;
        this.interphoneChangeVisible = boardConfig.interphoneVisible;
        this.kartChangeVisible = boardConfig.kartChangeVisible;
      }
    });
  }

  pitIn() {
    this.audioService.playFile('assets/box_box_box_box.mp3');
    this.raceManagerService.pitIn(
        this.selectedDriver,
        this.refuel,
        this.tyreChange,
        this.interphoneChange,
        this.kartChange);
  }

  pitOut() {
    this.raceManagerService.pitOut().subscribe(result => {
      if (result) {
        this.resetPitLane();
      }
    });
  }

  updateDriverId() {
    this.pitLaneConfigService.updateDriverId(this.selectedDriver);
  }

  updateTyreChange() {
    this.pitLaneConfigService.updateTyreChange(this.tyreChange);
  }

  updateRefueling() {
    this.pitLaneConfigService.updateRefuel(this.refuel);
  }

  updateInterphoneChange(){
    this.pitLaneConfigService.updateInterphoneChange(this.interphoneChange);
  }

  updateKartChange(){
    this.pitLaneConfigService.updateKartChange(this.kartChange);
  }

  async resetPitLane() {
    await this.pitLaneConfigService.updateRefuel(false);
    await this.pitLaneConfigService.updateTyreChange(false);
    await this.pitLaneConfigService.updateInterphoneChange(false);
    await this.pitLaneConfigService.updateKartChange(false);

    const driver: Driver | undefined = this.driverService.driverWithMoreTimeFromLastStint();
    if (driver) {
      await this.pitLaneConfigService.updateDriverId(driver.id);
    }
  }
}
